import React from 'react';
import './Home.css'; 
import Particles from 'react-particles-js';

const divStyle = {
  width: '100%',
  height: '100%',
};

const particlesParams = {
  "particles": {
      "number": {
          "value": 100
      },
      "size": {
          "value": 3
      }
  },
  "interactivity": {
      "events": {
          "onhover": {
              "enable": true,
              "mode": "repulse"
          }
      }
  }
};

export default function Topic(props) {
    return (
      <div className="Home">
        <Particles params={particlesParams} style={divStyle} />
        <div className="HomeHeadings">
          <div className="HomeTitle">MACMORRIS</div>
          <div className="HomeSubTitle"> Connection Explorer</div>
        </div>
      </div> 
           
    );
}

